export const newUser = (work_no, username,password,email) => {
	// console.log( work_no, username,password,email);
	return new axios({
		url: baseUrl + '/users',
		data: {
			'work_no': work_no,
			'username':username,
			'password': password,
			'email':email
		},
		method: POST
	})
};

